.module--accordion {

    .module-title,
    .module-text {
        h1,h2,h3 {
            #pills-adults & {
                color: $adults;
            }

            #pills-minors & {
                color: $minors;
            }

            #pills-children & {
                color: $children;
            }
            #pills-guardians & {
                color: $guardians;
            }
        }
    }

    .card {
        border: none;
        margin-bottom: 1rem;

        .card-header {
            border: none;
            background-color: $accordion-bg;
            padding: 2rem;

            @include media-breakpoint-down(md) {
                padding: 1.5rem;
            }

            .btn-link {
                padding-left: 0;
                padding-right: 0;
                width: 100%;
                text-align: left;
                font-size: $font-size-base * 1.5;
                font-weight: $font-weight-bold;
                color: $gray-600;
                position: relative;

                @include media-breakpoint-down(md) {
                    padding-right: 4rem;
                    // font-size: $font-size-base * 1.2;
                }

                &::after {
                    content: "\e90a";
                    float: right;
                    font-size: $font-size-base * 2.5;
                    color: $orange;
                    position: absolute;
                    top: 50%;
                    transform: translateY(-50%);
                    right: 0;
                    font-family: 'icomoon';
                    speak: never;
                    font-style: normal;
                    font-weight: normal;
                    font-variant: normal;
                    text-transform: none;
                    line-height: 1;
                    -webkit-font-smoothing: antialiased;
                    -moz-osx-font-smoothing: grayscale;

                    @include media-breakpoint-down(md) {
                        font-size: $font-size-base * 1.7;
                    }

                    #pills-adults & {
                        color: $adults;
                    }
                    #pills-minors & {
                        color: $minors;
                    }
                    #pills-children & {
                        color: $children;
                    }
                    #pills-guardians & {
                        color: $guardians;
                    }

                }



                &.collapsed {
                    &::after {
                        content: "\e90c";
                    }
                }

            }
        }

        .card-body {
            background-color: $accordion-bg;
            padding: 2rem;
            padding-top: 0;
            font-size: $font-size-base * 1.5;
            line-height: $font-size-base * 2.5;
            font-weight: $font-weight-light;

            @include media-breakpoint-down(md) {
                padding-top: 1rem;
            }
        }

    }



}