// -----------------------------------------------------------------------------
// This file contains all styles related to the hero component.
// -----------------------------------------------------------------------------

.module--hero {
    background: rgb(255, 255, 255);
    background: -moz-linear-gradient(180deg, rgba(255, 255, 255, 1) 0%, rgba(255, 255, 255, 1) 24%, rgba(204, 233, 241, 1) 100%);
    background: -webkit-linear-gradient(180deg, rgba(255, 255, 255, 1) 0%, rgba(255, 255, 255, 1) 24%, rgba(204, 233, 241, 1) 100%);
    background: linear-gradient(180deg, rgba(255, 255, 255, 1) 0%, rgba(255, 255, 255, 1) 24%, rgba(204, 233, 241, 1) 100%);
    filter: progid:DXImageTransform.Microsoft.gradient(startColorstr="#ffffff", endColorstr="#cce9f1", GradientType=1);
    color: $body-color;
    position: relative;
    margin-bottom: 5rem;
    max-height: $hero-height-default;
    min-height: $hero-height-default;


    @include media-breakpoint-down(md) {
        margin-bottom: 8rem;
        max-height: $hero-height-mobile;
        min-height: $hero-height-mobile;
    }
    @include media-breakpoint-between(md, lg) {
        max-height: $hero-height-default;
        min-height: $hero-height-default;
    }
          
    
    /*
    &.is--smaller {
        max-height: $hero-height-small;
        min-height: $hero-height-small;

        @include media-breakpoint-down(md) {
            margin-bottom: 8rem;
            max-height: $hero-height-mobile-small;
            min-height: $hero-height-mobile-small;
        }
        .hero-image img {
            max-height: $hero-height-small;
            min-height: $hero-height-small;
            @include media-breakpoint-down(md) {
                max-height: $hero-height-mobile-small;
                min-height: $hero-height-mobile-small;
            }
        }
    }
    */

    .hero-overlay-container {
        height: calc(100% - 105px);
        display: flex;
        flex-direction: column;
        justify-content: center;
        margin-top: 105px;
    }

    .hero-overlay {
        position: absolute;
        top: 0;
        left: 0;
        height: 100%;
        width: 100%;
        z-index: 1;
        background: transparent url("../images/hero-pattern.png") no-repeat;
    }

    .hero-overlay-pre {
        @include font-size(1.25rem);
        // font-weight: $font-weight-bold;
        margin-bottom: 1rem;

        * {
            color: #033349;
        }
  

        @include media-breakpoint-down(md) {
            font-size: calc(0.5rem + 1.5vw);
            margin-top: 0;
        }

        @include media-breakpoint-down(sm) {
            margin-top: 0;
        }

        a {
            color: $white;
            text-decoration: none;
            &::after {
                content: "";
                width: 2px;
                height: 100%;
                background-color: $white;
                display: inline-block;
                position: relative;
                top: 7px;
                margin: 0 1rem;
                @include media-breakpoint-down(md) {
                    height: 85%;
                    top: 5px;
                }
                @include media-breakpoint-down(sm) {
                    height: 18px;
                    top: 3px;
                }
            }
            + span {
                color: #601507;
                font-weight: $font-weight-bold;
                @include media-breakpoint-down(md) {
                    padding-top: 6px;
                    display: inline-block;
                }
            }
        }

    }

    .hero-overlay-title {

        * {
            @include font-size(3rem);
            font-weight: $font-weight-black;
            line-height: 3.25rem;
            color: $primary;
            margin: 0;
        }

        @include media-breakpoint-down(sm) {
            @include font-size(2.5rem);
            // margin-top: 1rem;
            line-height: calc(1.5rem + 3vw);

            h1 {
                @include font-size(2.5rem);

                line-height: calc(1.5rem + 3vw);
                margin-bottom: 0;
            }

            br {
                display: none;
            }

        }
    }
    
    .hero-overlay-text {
        line-height: 2.5rem;
        margin-top: 1.5rem;
        @include font-size(2rem);

        @include media-breakpoint-down(sm) {
            font-size: $font-size-base * 1.3;
            line-height: inherit;
        }

        &.msg-danger,
        &.msg-success {
            @include media-breakpoint-up(md) {
                margin-top: 0;
            }
            padding: .75rem 1.25rem;
        }

        &.msg-danger {
            @include alert-variant(#f8d7da, #f5c6cb, #721c24);
        }
        &.msg-success {
            @include alert-variant(#d4edda, #c3e6cb, #155724);
        }

    }


    
    .hero-overlay-additional {
        

        .btn {
            color: $body-color;
            padding: 0;
        }

        &.two--links {

            @include media-breakpoint-up(md) {
                display: flex;
                align-items: center;
                margin-top: 3rem;
            }

            > a {
                font-size: 2rem;
                font-weight: $font-weight-bold;
                text-decoration: none;

                @include media-breakpoint-down(md) {
                    font-size: 1.5rem;
                }

                @include media-breakpoint-down(sm) {
                    font-size: 6vw;
                }

                span {
                    color: $primary;
                    transform: translateY(5px);
                }

                @media (hover: hover) {
                    &:hover {
                        color: $primary;
                    }
                }

                &:first-child {
                    font-size: 2.5rem;
                    font-weight: 700;
                    @include media-breakpoint-down(md) {
                        padding-right: 1rem;
                         font-size: 1.5rem;
                    }
                    @include media-breakpoint-down(sm) {
                        font-size: 6vw;
                    }
                }

                &:last-child {
                    margin-left: auto;
                    @include media-breakpoint-down(sm) {
                        margin-top: 1rem;
                    }
                }
            }
        }

        /*
        p {
            font-size: 1.25rem;
            padding-left: .75rem;
            padding-right: .75rem;
            a {
                padding: 0;
                vertical-align: initial;
            }
        }
        */


        @include media-breakpoint-down(sm) {

            .btn {
                font-size: $font-size-base * 1;
            }
            p {
                font-size: $font-size-base * 1;
            }
        }
        
    }
    

    @include hero-overlay-form;

    @include media-breakpoint-up(xxl) {
        .hero-overlay-pre,
        .hero-overlay-title,
        .hero-overlay-text,
        .hero-overlay-form,
        .hero-overlay-additional {
            max-width: $hero-text-container-max-width;
            position: relative;
            z-index: 1;
        }

        .hero-overlay-additional {
            max-width: $hero-text-container-max-width + 50;
        }
        

        // smaller
        &.is--smaller {
            .hero-overlay-pre,
            .hero-overlay-title,
            .hero-overlay-text,
            .hero-overlay-form,
            .hero-overlay-additional {
                max-width: 1150px;
            }

            @include media-breakpoint-down(sm) {
                .hero-overlay-text {
                    max-width: 90%;
                }
            }

        }

    }

    @media (max-width: 1100px) {
        .hero-overlay-pre,
        .hero-overlay-title,
        .hero-overlay-text,
        .hero-overlay-form,
        .hero-overlay-additional {
            padding-right: 50px;
        }
    }

    @include media-breakpoint-down(md) {
        .hero-overlay-pre,
        .hero-overlay-title,
        .hero-overlay-text,
        .hero-overlay-form,
        .hero-overlay-additional {
            padding-right: 0;
        }
    }



}