.smiley-selector input {
    margin: 0;
    padding: 0;
    appearance: none;
}

.smiley-selector {
    position: relative;
    margin-top: 2rem;

    &::after {
        content: attr(data-selected-val);
        display: inline-block;
        position: relative;
        top: -20px;
        font-size: $font-size-base * 1.75;
        color: #000;
        font-weight: $font-weight-light;
        text-transform: uppercase;

        @media (max-width: 900px) {
            position: static;
            font-size: $font-size-base * 1.25;
            display: block;
        }

    }

    &.selected--nothing {
        &::after {
            color: #999;
            opacity: .6;
        }
    }

}


.red {
    background-image: url(../images/smiley-5.svg);
}

.orange {
    background-image: url(../images/smiley-4.svg);
}

.yellow {
    background-image: url(../images/smiley-3.svg);
}

.lightgreen {
    background-image: url(../images/smiley-2.svg);
}

.green {
    background-image: url(../images/smiley-1.svg);
}


.smiley-selector input:active+.smiley-label {
    opacity: .9;
}

.smiley-selector input:checked+.smiley-label {
    -webkit-filter: none;
    -moz-filter: none;
    filter: none;
}

.smiley-label {
    cursor: pointer;
    background-size: contain;
    background-repeat: no-repeat;
    display: inline-block;
    width: 100px;
    height: 70px;
    margin: 0;
    transition: $transition-custom;
    filter: grayscale(100%) opacity(.3);


    
    @include media-breakpoint-down(sm) {
        width: 75px;
    }

    @include media-breakpoint-down(xs) {
        width: 15%;
        height: 60px;
    }

}

.smiley-label:hover {
    filter: initial;
    filter: grayscale(0) opacity(1);
}