$temp-alert-image-dim: 200px;

.alert {

    margin-bottom: 1rem;

    > p:last-of-type {
        margin-bottom: 0;
    }

    #pills-adults & {
        color: $adults;
        @include alert-variant(lighten($adults, 30%), lighten($adults, 30%), #222);
    }

    #pills-minors & {
        color: $minors;
        @include alert-variant(lighten($minors, 30%), lighten($minors, 30%), #222);
    }

    #pills-children & {
        color: $children;
        @include alert-variant(lighten($children, 30%), lighten($children, 30%), #222);
    }

    #pills-guardians & {
        color: $guardians;
        @include alert-variant(#c39aba, #c39aba, #222);
    }

    &.color--gray {
        > small,
        > p {
            color: #222;
        }
    }
    
    &--has-image {
        margin-top: 8rem;
        margin-bottom: 30px;

        @include media-breakpoint-up(lg) {
            > small,
            > p:first-of-type {
                padding-left: ($temp-alert-image-dim + 50);
            }
        }
    }

    // end teaser
    &.alert-success.alert--has-image {

        @include media-breakpoint-up(lg) {
            > h1,
            > small,
            > p {
                padding-left: ($temp-alert-image-dim + 50);
            }

            > h1 {
                margin-top: -8rem;
            }
            > small {
                margin-top: 3.5rem;
                display: block;
            }

        }
        
    }

}

.alert-default {
    @include alert-variant(#eeebe3, #eeebe3, #222);
    padding: 2rem 2.5rem;
}

.alert-img-container {
    width: $temp-alert-image-dim;
    height: $temp-alert-image-dim;
    position: relative;
    top: 0;
    margin: 0 auto;
    margin-top: -($temp-alert-image-dim / 2);

    .alert-success & {
        margin-top: -($temp-alert-image-dim - 70);
    }

    @include media-breakpoint-up(lg) {
        position: absolute;
    }

    img {
        border: 1px solid $primary;
        border-radius: 50%;

        .alert-success & {
            border: none;
        }

    }

    
}

p + .alert {
    margin-top: 1rem;
}