.mb-30 {
  margin-bottom: 30px;
}

.h1,
h1 {
  color: #171717;
  font-size: 2rem;
  font-weight: 700;
  margin-bottom: 2rem;
}

.site-main .h1, .site-main
h1 {
  color: #ce3a31;
}

h2, .h2 {
  color: #ce3a31;
  font-weight: 700;
  margin-bottom: 1.5rem;
}

h3, .h3 {
  color: #ce3a31;
  font-weight: 700;
  margin-bottom: 1.3rem;
}

h4, .h4 {
  color: #222;
  font-weight: 700;
  margin-bottom: 3rem;
}

p {
  font-size: 1.5rem;
}

.site-main ul > li {
  font-size: 1.5rem;
}

.site-header {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  z-index: 2;
  padding-top: 50px;
}

@media (max-width: 1499.98px) {
  .site-header {
    padding-top: 20px;
  }
}

.site-header .navbar {
  padding: 0;
  position: static;
  justify-content: flex-start;
  /*
        @include media-breakpoint-down(md) {
            padding-top: 1.5rem;
        }
        */
}

.site-header .navbar-toggler {
  margin-right: 2rem;
  padding: 0;
  border: none;
  outline: none;
}

@media (min-width: 1600px) {
  .site-header .navbar-toggler {
    position: absolute;
    left: 0;
    left: -70px;
  }
}

.site-header .navbar-toggler .navbar-toggler-icon-animated {
  width: 24px;
  height: 20px;
  position: relative;
  margin: 0px;
  transform: rotate(0deg);
  cursor: pointer;
  transition: all 0.2s ease-in-out;
}

.site-header .navbar-toggler .navbar-toggler-icon-animated span {
  display: block;
  position: absolute;
  height: 4px;
  width: 100%;
  border-radius: 0;
  opacity: 1;
  left: 0;
  transform: rotate(0deg);
  transition: all 0.2s ease-in-out;
  background-color: #29a1c6;
}

.site-header .navbar-toggler .navbar-toggler-icon-animated span:nth-child(1) {
  top: 0;
}

.site-header .navbar-toggler .navbar-toggler-icon-animated span:nth-child(2) {
  top: 10px;
  width: 70%;
}

.site-header .navbar-toggler .navbar-toggler-icon-animated span:nth-child(3) {
  top: 20px;
}

.site-header .navbar-toggler .navbar-toggler-icon-animated:hover span:nth-child(2) {
  width: 100%;
}

.site-header .navbar-toggler:not(.collapsed) .navbar-toggler-icon-animated span:nth-child(2) {
  width: 100%;
}

.is--menu-visible .site-header .navbar-toggler {
  opacity: 0.1;
}

.site-header .navbar-brand {
  padding: 0;
  margin-right: 3rem;
}

.site-header .navbar-brand.brand-primary {
  position: absolute;
  right: 40px;
  top: 0;
}

@media (max-width: 1499.98px) {
  .site-header .navbar-brand.brand-primary {
    top: 30px;
  }
}

.site-header .navbar-brand.brand-primary::before {
  content: "";
  position: absolute;
  top: -50px;
  left: 0;
  width: 100%;
  height: 12px;
  background-color: #033349;
}

@media (max-width: 991.98px) {
  .site-header .navbar-brand.brand-primary::before {
    width: 105px;
    left: auto;
  }
}

@media (max-width: 767.98px) {
  .site-header .navbar-brand.brand-primary::before {
    width: 95px;
  }
}

@media (max-width: 991.98px) {
  .site-header .navbar-brand.brand-primary {
    right: 0;
    top: 8px;
    text-align: right;
  }
  .site-header .navbar-brand.brand-primary img {
    width: 80%;
  }
}

@media (max-width: 767.98px) {
  .site-header .navbar-brand.brand-primary {
    margin-left: auto;
    position: relative;
  }
  .site-header .navbar-brand.brand-primary img {
    width: 70%;
  }
}

@media (max-width: 991.98px) {
  .site-header .navbar-brand {
    margin-right: 0;
    transform: scale(0.8);
  }
}

.site-header .navbar-collapse {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  transition: none;
}

.is--menu-visible .site-header .navbar-collapse {
  position: fixed;
  background-color: rgba(0, 74, 111, 0.92);
  z-index: 2;
}

.site-header .navbar-collapse .navbar-nav {
  justify-content: center;
  height: 100vh;
}

@media (max-width: 767.98px) {
  .site-header .navbar-collapse .navbar-nav {
    height: calc(100vh - 200px);
  }
}

.site-header .navbar-collapse .navbar-nav > li {
  margin-bottom: 1.5rem;
}

@media (max-width: 767.98px) {
  .site-header .navbar-collapse .navbar-nav > li {
    margin-bottom: 1rem;
  }
}

.site-header .navbar-collapse .navbar-nav > li a {
  font-size: 2rem;
  line-height: 2rem;
  font-weight: 700;
  transition: 0.8s cubic-bezier(0.16, 1, 0.3, 1);
}

@media (max-width: 767.98px) {
  .site-header .navbar-collapse .navbar-nav > li a {
    font-size: 1.3rem;
    line-height: 1.3rem;
  }
}

.site-header .navbar-collapse .navbar-nav > li a > span {
  margin-left: 1.5rem;
}

.site-header .navbar-collapse .navbar-nav > li:hover a {
  padding-left: 2rem;
  color: #fff;
}

.site-header .navbar-collapse .close-menu {
  position: absolute;
  right: 0;
  top: 0;
  height: 50px;
  width: 50px;
  display: block;
  font-size: 2.5rem;
  margin-top: 5rem;
  color: #fff;
  cursor: pointer;
  transition: 0.8s cubic-bezier(0.16, 1, 0.3, 1);
  opacity: 0;
  transform: rotate(45deg);
  z-index: 1;
}

@media (max-width: 767.98px) {
  .site-header .navbar-collapse .close-menu {
    top: -30px;
  }
}

.site-header .navbar-collapse .close-menu::after {
  content: "\e90c";
  float: right;
  font-size: 2.5rem;
  color: #fff;
  position: absolute;
  right: 2.3rem;
  font-family: 'icomoon';
  speak: never;
  font-style: normal;
  font-weight: normal;
  font-variant: normal;
  text-transform: none;
  line-height: 1;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  transition: all 0.2s ease-in-out;
}

.site-header .navbar-collapse .close-menu:hover::after {
  color: #29a1c6;
}

.is--menu-visible .site-header .navbar-collapse .close-menu {
  opacity: 1;
}

