// -----------------------------------------------------------------------------
// This file contains very basic styles.
// -----------------------------------------------------------------------------

/*
button-variant(
    $background, 
    $border, 
    $hover-background: darken($background, 7.5%), 
    $hover-border: darken($border, 10%), 
    $active-background: darken($background, 10%), 
    $active-border: darken($border, 12.5%)) 
*/

body,
.site-main {
    overflow-x: hidden;
}

::selection {
    background: $primary;
    color: #fff;
}



.btn-light {
    @include button-variant($white, #fff, $primary, $primary, $primary, transparent);
    padding: 2rem 2.5rem;
    color: $orange;
    font-weight: $font-weight-bold;
    font-size: 1.5rem;

    @include media-breakpoint-down(sm) {
        padding: 1rem 1rem;
        font-size: 1rem;
    }

    &.disabled,
    &:disabled {
        padding: .875rem 2.5rem;
        text-align: left;
        pointer-events: auto !important;
        @include media-breakpoint-down(sm) {
            padding: .25rem 1rem;
        }
    }

}

.btn-orange {
    @include button-variant($orange, $orange, $primary, $primary, $primary, transparent);
    padding: 1.25rem 2.5rem;
    color: #fff;
    font-weight: $font-weight-bold;
    font-size: 1.5rem;
    @include media-breakpoint-down(sm) {
        padding: 1rem 2rem;
        font-size: 1.2rem;
    }
}

.btn-link {
    @include button-variant(transparent, transparent, transparent, transparent, transparent, transparent);
    font-size: 1.25rem;
    text-decoration: none;
    text-align: left;

    &.text-orange {
        padding: 0;
        color: $orange;
        display: block;
        text-align: left;
        &:active {
            color: $orange !important;
        }
    }

}

.btn {
    &:focus {
        box-shadow: none;
    }

    &.disabled,
    &:disabled {
        opacity: 1;
    }

}



span.icon-arrow-right {
    margin-left: .5rem;
    // transform: translateY(3px);
    display: inline-block;
    /*
    @include media-breakpoint-down(sm) {
       transform: translateY(1px);
    }
    */
}

span.icon-arrow-left {
    margin-right: .5rem;
    transform: translateY(2px);
    display: inline-block;
    @include media-breakpoint-down(sm) {
        transform: translateY(1px);
    }
}

.btn-link:not(:disabled):not(.disabled).active:focus,
.btn-link:not(:disabled):not(.disabled):active:focus,
.show>.btn-link.dropdown-toggle:focus {
    box-shadow: none;
}

.btn-light:not(:disabled):not(.disabled).active:focus,
.btn-light:not(:disabled):not(.disabled):active:focus,
.show>.btn-light.dropdown-toggle:focus {
    box-shadow: none;
}


.list-dots {
    list-style: none;
    counter-reset: li;
    padding-left: 90px;
    margin-bottom: 6rem;
    li {

        &:first-child {
            padding-top: 1.5rem;
        }

        counter-increment: li;
        font-size: $font-size-base * 1.5;
        margin-bottom: 2rem;

        
    
        &::before {
            position: absolute;
            content: counter(li) ".";
            color: #fff;
            background-color: $orange;
            display: inline-block;
            width: 1em;
            margin-left: -90px;
            margin-right: 20px;
            border-radius: 50%;
            width: 70px;
            height: 70px;
            text-align: center;
            font-size: 2rem;
            padding-top: 11px;
            padding-left: 6px;
            margin-top: -20px;
        }
    }
}

.line-through {
    display: block;
    text-decoration: line-through;
    font-style: italic;
    color: #888;
}