.card {
    display: block;
    height: 100%;
    background: #f2f5f6;
    border: none;


    .card-img-container,
    .card-img-overlay-container {
        overflow: hidden;
    }

    .card-img {
        transition: $transition-base;
        height: 100%;
        object-fit: cover;
    }

}

.card-img-overlay,
.card-body {

    padding: 2rem;

    small {
        text-transform: uppercase;
        font-size: $font-size-base * 0.875;
        display: inline-block;
    }

    .card-text {
        margin-bottom: .5rem;
    }

    .card-title {
        margin-bottom: 0;
    }

    .card-icon {
        position: absolute;
        right: 3rem;
        bottom: 1rem;
        font-size: $font-size-base * 2;
        @include media-breakpoint-up(xl) {
            bottom: 2rem;
        }
    }
    
    .card-title .card-title-text,
    .card-icon {
        transition: $transition-base;
    }

}

.card:hover {
    .card-img {
        transform: scale(1.03);
    }
    .card-title .card-title-text,
    .card-icon {
        color: $orange;
        .play-btn {
            transform: scale(1.2);
        }
    }
}

// big card-teaser
.card-img-overlay-container {
    height: 100%;
    @include media-breakpoint-down(md) {
        min-height: 400px;
    }
}
.card-img-overlay {
    background: linear-gradient(to bottom, rgba(0, 0, 0, 0) 50%, rgba(0, 0, 0, 1) 100%);
    
    .card-img {
        height: 100%;
    }

    .card-title {
        margin-bottom: 0;

        .card-title-text {
            font-weight: $font-weight-bold;
            font-size: $font-size-base * 2;
            line-height: 2.5rem;
            margin-bottom: 0;
            max-width: 90%;
            color: $white;
            @include media-breakpoint-up(xl) {
                max-width: 60%;
            }
        }
    }

}

// normal card-teasers
.card-body {
    background-color: #f2f5f6;
    color: #222;
    padding: 1.5rem;
    
    small {
        color: #6a6c71;
        font-weight: $font-weight-light;
    }
    .card-title {
        .card-title-text {
            font-weight: $font-weight-bold;
            font-size: $font-size-base * 1.5;
            line-height: 2rem;
            color: #222;
        }
    }
    .card-icon {
        position: static;
        text-align: right;
    }
}

.card-link {
    min-height: 240px;
    background-color: #f0ebdd;
    .card-body {
        background-color: #f0ebdd;
    }
}

// card link video
.card.card-link-video {
    .card-img-overlay {
        .card-title {
            .card-title-text {
                max-width: 90%;
                font-weight: $font-weight-bold;
                font-size: $font-size-base * 1.5;
                line-height: 2rem;
            }
        }

    }
    .play-btn {
        width: 50px;
        height: 44px;
        margin-left: -20px;
        bottom: 0;
        top: auto;
        &::before {
            margin-left: 18px;
            border-width: 10px 0 10px 15px;
        }
    }
}

// card link url 
.card.card-link-url {
    .card-body { 
        small {
            color: $orange;
            font-weight: $font-weight-bold;
        }
        .card-title {
            max-width: 95%;
        }
        .card-icon {
            position: absolute;
            right: 20px;
        }
        .card-link-text {
            color: $orange;
            font-size: $font-size-base * 1.25;
            display: block;
            max-width: 80%;
            overflow: hidden;
            white-space: nowrap;
            text-overflow: ellipsis;
        }
    }
    
}