.video-wrapped {
    background: black no-repeat center center;
    background-size: cover;
    color: #fff;
    position: relative;
    padding-bottom: 56.25%;
    height: 0;
    width: 100%;
    overflow: hidden;
    margin-bottom: 2rem;
    margin-top: 2rem;
    border-radius: 20px;

    #pills-video & {
        color: #000;
    }

}

.video-wrapped .video-wrapped-nojs {
    padding: 20px;
}

.video-wrapped .video-wrapped-play {
    text-align: center;
    font-size: 30px;
    position: absolute;
    left: 0;
    right: 0;
    top: 50%;
    text-shadow: 1px 1px 3px black;
    font-size: 16px;
    transform: translateY(-50%);
    margin-top: 50px;
    #pills-video & {
        margin-top: 0;
    }
}

.play-btn {
    position: absolute;
    left: 50%;
    height: 87px;
    width: 100px;
    background: $orange;
    cursor: pointer;
    top: -100px;
    margin-left: -50px;
    transition: all 0.2s ease-in-out;
    background: transparent url("../images/btn.play.png") no-repeat center center;
    background-size: cover;
    
    @include media-breakpoint-down(xs) {
        height: 70px;
        width: 70px;
        margin-left: -35px;
        top: -70px;
    }

    #pills-video & {
        background: transparent url("../images/btn.play-alt.png") no-repeat center center;
        background-size: cover;
        width: 300px;
        height: 260px;
        top: 0;
        margin-left: -150px;

        
        @include media-breakpoint-down(md) {
            height: 156px;
            width: 180px;
            margin-left: -90px;
        }
        

        @include media-breakpoint-down(sm) {
            height: 104px;
            width: 120px;
            margin-left: -60px;
        }

    }

}

.play-btn:hover {
    transform: scale(1.2);
}


.video-wrapped .video-wrapped-play .play-text {
    padding-top: 30px;
    display: block;

    #pills-video & {
        &:not(.is--subline) {
            padding-top: 25%;
        }
    }

    &.is--subline {
        padding-top: .5rem;
        a {
            color: #fff;
            font-size: .9rem;

            #pills-video & {
                color: #000;
            }

        }
    }

    @include media-breakpoint-down(xs) {
        padding-top: 15px;
    }
}

.video-wrapped .video-wrapped-play .small {
    font-size: 50%;
}

.video-wrapped iframe {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
}
