label {
    font-size: 1.5rem;
    color: $gray-600;
    font-weight: $font-weight-bold;

    &:not(.d-block) {
        font-size: $font-size-base * 1.75;
        font-weight: $font-weight-light;
        color: #999;
        text-transform: uppercase;
        padding-left: .5rem;
        cursor: pointer;

        @include media-breakpoint-down(md) {
            font-size: $font-size-base * 1.3;
            word-wrap: break-word;
        }

        > span {
            text-transform: none;
            display: block
        }
    }

    + span {
        color: #999;
        font-weight: $font-weight-light;
        font-size: $font-size-base * 1.75;
    }


}

.row--sub label {
    color: #999;
    font-weight: 300;
    font-size: $font-size-base * 1.75;
}

.custom-control-label:after,
.custom-control-label:before {
    top: 6px;
    border-radius: $border-radius;
    @include media-breakpoint-down(md) {
        top: 3px;
    }
}

.custom-control {
    margin-bottom: 1rem;
}

.custom-select {
    height: 5rem;
    border-radius: $border-radius;
    font-size: 1.5rem;
    padding-left: 2rem;
}

.custom-text {
    height: 5rem;
    border-radius: $border-radius;
    font-size: 1.5rem;
    padding-left: 2rem;
}


.child-field {
    padding: 1.5rem 2.5rem;
}

.form-row {
    margin-bottom: 2rem;

    @include media-breakpoint-down(sm) {
        margin-right: 0;
        margin-left: 0;
    }


}

textarea.form-control {
    font-size: $font-size-base * 1.75;
    padding: .5rem 1rem;
    color: #999;
    @include media-breakpoint-down(md) {
        font-size: $font-size-base * 1.3;
    }

}

.form-text p {
    font-size: $font-size-base * 1.75;
    @include media-breakpoint-down(md) {
        font-size: $font-size-base * 1.3;
    }

}

.form-scrollbox {
    max-height: 300px;
    overflow-y: scroll;
    border: 1px solid #222;
    padding: .5rem 1rem;
    margin: 1rem 0;
}

@include media-breakpoint-down(sm) {
    .form-row>.col,
    .form-row>[class*=col-] {
        padding-right: 0;
        padding-left: 0;
        padding-left: 0;
    }
}

// validation

.custom-control-input.is-valid:checked~.custom-control-label:before,
.was-validated .custom-control-input:valid:checked~.custom-control-label:before {
    background-color: #fff;
}