.h1,
h1 {
    color: $gray-800;
    font-size: $font-size-base * 2;
    font-weight: $font-weight-bold;
    margin-bottom: 2rem;

    .site-main & {
        color: $orange;
    }

}



h2,
.h2 {
    color: $orange;
    font-weight: $font-weight-bold;
    margin-bottom: 1.5rem;
}

h3,
.h3 {
    color: $orange;
    font-weight: $font-weight-bold;
    margin-bottom: 1.3rem;
}

h4,
.h4 {
    color: #222;
    font-weight: $font-weight-bold;
    margin-bottom: 3rem;
}

p {
    font-size: $font-size-base * 1.5;
}

.site-main {
    ul {
        > li {
            font-size: $font-size-base * 1.5;
        }
    }
}