// -----------------------------------------------------------------------------
// This file contains all styles related to the header of the site/application.
// -----------------------------------------------------------------------------

.site-header {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    z-index: 2;
    padding-top: 50px;

    @include media-breakpoint-down(lg) {
        padding-top: 20px;
    }

    .navbar {
        padding: 0;
        position: static;
        justify-content: flex-start;

        /*
        @include media-breakpoint-down(md) {
            padding-top: 1.5rem;
        }
        */
    }

    .navbar-toggler {
        
        margin-right: 2rem;
        padding: 0;
        border: none;
        outline: none;
  
        @media (min-width: 1600px) {
            position: absolute;
            left: 0;
            left: -70px;
        }

        .navbar-toggler-icon-animated {
            width: 24px;
            height: 20px;
            position: relative;
            margin: 0px;
            transform: rotate(0deg);
            cursor: pointer;
            transition: $transition-base;

            span {
                display: block;
                position: absolute;
                height: 4px;
                width: 100%;
                border-radius: 0;
                opacity: 1;
                left: 0;
                transform: rotate(0deg);
                transition: $transition-base;
                background-color: $primary;
                
                &:nth-child(1) {
                    top: 0;
                }
                &:nth-child(2) {
                    top: 10px;
                    width: 70%;
                }
                &:nth-child(3) {
                    top: 20px;
                }



            }
 

            
            &:hover {
                span {
                    &:nth-child(2) {
                        width: 100%;
                    }
                }
            }
            

        }

        &:not(.collapsed) {
            .navbar-toggler-icon-animated {
                span {
                    &:nth-child(2) {
                        width: 100%;
                    }
                }
            }
        }

        .is--menu-visible & {
            opacity: 0.1;
        }

    }

    .navbar-brand {
        padding: 0;
        margin-right: 3rem;

        &.brand-primary {
            position: absolute;
            right: 40px;
            top: 0;
            @include media-breakpoint-down(lg) {
                top: 30px;
            }
            &::before {
                content: "";
                position: absolute;
                top: -50px;
                left: 0;
                width: 100%;
                height: 12px;
                background-color: $secondary;
                @include media-breakpoint-down(md) {
                    width: 105px;
                    left: auto;
                }
                @include media-breakpoint-down(sm) {
                    width: 95px;
                }
            }
           
            @include media-breakpoint-down(md) {
                right: 0;
                top: 8px;
                text-align: right;
                img {
                    width: 80%;
                }
            }

            @include media-breakpoint-down(sm) {
                margin-left: auto;
                position: relative;
                img {
                    width: 70%;
                }
            }
        }

        @include media-breakpoint-down(md) {
            margin-right: 0;
            transform: scale(0.8);
        }

    }

    .navbar-collapse {
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        transition: none;

        .is--menu-visible & {
            position: fixed;
            background-color: rgba(0, 74, 111, .92);
            z-index: 2;
        }

        .navbar-nav {
            justify-content: center;
            height: 100vh;
            @include media-breakpoint-down(sm) {
                height: calc(100vh - 200px);
            }
            > li {
                margin-bottom: 1.5rem;
                @include media-breakpoint-down(sm) {
                    margin-bottom: 1rem;
                }
                a {
                    font-size: $font-size-base * 2.0;
                    line-height: $font-size-base * 2.0;
                    font-weight: $font-weight-bold;
                    transition: $transition-custom;

                    @include media-breakpoint-down(sm) {
                       font-size: $font-size-base * 1.3;
                       line-height: $font-size-base * 1.3;
                       // color: $white;
                    }

                    > span {
                        margin-left: 1.5rem;
                    }    
                }
                &:hover {
                    a {
                        padding-left: 2rem;
                        color: $white; 
                    }
                }

            }
        }

        .close-menu {
            position: absolute;
            right: 0;
            top: 0;
            height: 50px;
            width: 50px;
            display: block;
            font-size: 2.5rem;
            margin-top: 5rem;
            color: $white;
            cursor: pointer;
            transition: $transition-custom;
            opacity: 0;
            transform: rotate(45deg);
            z-index: 1;

            @include media-breakpoint-down(sm) {
                top: -30px;
            }
           
            &::after {
                content: "\e90c";
                float: right;
                font-size: $font-size-base * 2.5;
                color: $white;
                position: absolute;
                right: 2.3rem;
                font-family: 'icomoon';
                speak: never;
                font-style: normal;
                font-weight: normal;
                font-variant: normal;
                text-transform: none;
                line-height: 1;
                -webkit-font-smoothing: antialiased;
                -moz-osx-font-smoothing: grayscale;
                transition: $transition-base;
            }

            &:hover {
                &::after {
                    color: $primary;
                }
            }

            

            .is--menu-visible & {
                opacity: 1;
            }
        }

    }
}