.site-header-sticky {
    background: rgb(255, 255, 255);
    background: -moz-linear-gradient(180deg, rgba(255, 255, 255, 1) 0%, rgba(255, 255, 255, 1) 24%, rgba(204, 233, 241, 1) 100%);
    background: -webkit-linear-gradient(180deg, rgba(255, 255, 255, 1) 0%, rgba(255, 255, 255, 1) 24%, rgba(204, 233, 241, 1) 100%);
    background: linear-gradient(180deg, rgba(255, 255, 255, 1) 0%, rgba(255, 255, 255, 1) 24%, rgba(204, 233, 241, 1) 100%);
    filter: progid:DXImageTransform.Microsoft.gradient(startColorstr="#ffffff", endColorstr="#cce9f1", GradientType=1);
    position: fixed;
    top: 0;
    width: 100%;
    z-index: 1;
    padding: 2rem 0;
    transform: translateY(-100%);
    transition: $transition-custom;

    @include media-breakpoint-down(lg) {
        display: none !important;
    }

    &.is--visible {
        transform: translateY(0);
    }

    .site-header-sticky-bg {
        position: absolute;
        top: 0;
        left: 0;
        height: 100%;
        width: 100%;
        z-index: 0;
        background: transparent url("../images/hero-pattern.png") no-repeat;
    }

    .navbar-toggler {
        top: 17px;
    }

    @include hero-overlay-form;
    .hero-overlay-form {
        margin-top: 0;
        .form-control {
            height: calc(1.55em + 1rem + 12px);
            font-size: 1.375rem;
            min-width: 440px;
        }
        .hero-overlay-button {
            min-width: inherit;
            margin-right: 50px;
            > button {
                font-size: 1.375rem;
                max-width: 220px;
                min-width: 220px;
                padding-left: 30px;
                padding-right: 30px;
            }
        }
    }

    .btn.btn-link {
        color: $body-color;
        padding: 0;
        display: flex;
        align-items: center;    
        font-size: 1.875rem;
        font-weight: $font-weight-bold;
        text-decoration: none;

        span {
            color: $primary;
        }

        @media (hover: hover) {
            &:hover {
                color: $primary;
            }
        }

        &:first-child {
            font-size: 2.5rem;
            font-weight: bolder;
        }
            
    }

    .navbar-brand.brand-primary {
        &::before {
            top: -17px;
            height: 8px;
        }
        top: -15px;
        text-align: center;
        img {
            width: 90px;
        }
    }

}