@charset "UTF-8";

// Variable overrides
$body-bg: #fff;
$body-color: #333;

$primary: #29a1c6;
$secondary: #033349;
$info: #2aade4;
$orange: #ce3a31;
$gray-200: #f2f5f6;
$gray-300: #e2e6e8;
$gray-600: #222;
$gray-800: #171717;

$font-family-base:"proxima-nova", "Myriad Pro", sans-serif;

$enable-responsive-font-sizes: true;

// Form
$line-height-lg: 1.55;
$input-placeholder-color: #c0c0c0;
$input-font-size-lg: 2.5rem;
$label-margin-bottom: 1.5rem;
$custom-control-indicator-size: 1.75rem;
$form-grid-gutter-width: 80px;

$custom-control-indicator-checked-color: $primary;
$custom-control-indicator-checked-bg: #fff;
$custom-control-indicator-checked-border-color: #adb5bd;
$custom-control-indicator-bg-size: 60% 60%;

$custom-control-indicator-focus-box-shadow: none;
$custom-control-indicator-focus-border-color: #adb5bd;

$custom-control-indicator-active-bg: #fff;
$custom-control-indicator-active-border-color: #adb5bd;

// Form validation
$form-feedback-valid-color: #999;
$form-feedback-icon-valid: none;

// Modal
$modal-backdrop-bg: #fff;
$modal-backdrop-opacity: 0.8;
$modal-inner-padding: 3.75rem;
$modal-header-padding-y: 3.5rem;
$modal-header-padding-x: 3rem;

// Pills
$nav-pills-border-radius: none;
$nav-pills-link-active-color: $orange;
$nav-pills-link-active-bg: transparent;

// Required
@import "node_modules/bootstrap/scss/functions";
@import "node_modules/bootstrap/scss/variables";
@import "node_modules/bootstrap/scss/mixins";

// Add more spacing classes
$spacers: (
    0: 0,
    1: ($spacer * .25),
    2: ($spacer * .5),
    3: $spacer,
    4: ($spacer * 1.5),
    5: ($spacer * 3),
    6: ($spacer * 6),
    7: ($spacer * 7),
    8: ($spacer * 8),
    9: ($spacer * 9),
    10: ($spacer * 10)
);

$theme-colors: map-remove($theme-colors, "secondary", "dark");
$colors: map-remove($colors, "blue", "indigo", "purple", "pink", "red", "yellow", "green", "teal", "cyan");
// $grid-breakpoints: map-remove($grid-breakpoints, "xs", "sm", "md", "lg", "xl");


$grid-breakpoints: (
    xs: 0,
    sm: 576px,
    md: 768px,
    lg: 992px,
    xl: 1500px
);

$container-max-widths: (
    sm: 660px,
    md: 920px,
    lg: 1400px,
    xl: 1440px
);


$enable-rounded: false;
$enable-transitions: true;


