
#theme-settings {
	position: fixed;
	top: 0;
	right: 0;
	height: 100%;
	z-index: 99999999;
	display: flex;
	flex-direction: column;
	width: 300px;
	background: #fff;
	box-shadow: 0 0 20px 0 rgba(0, 0, 0, .2);
	transition: all .2s ease-in;
	transform: translateX(320px);
}

#theme-settings h5 {
	position: relative;
	font-size: 14px;
	font-weight: 600;
}

#theme-settings .theme-settings-header {
	font-size: 16px;
	padding: 10px 20px;
}

#theme-settings .disabled {
	color: #d1d2d3!important
}

#theme-settings.theme-settings-open {
	transition-delay: .1s;
	transform: none!important
}

#theme-settings .theme-settings-open-btn {
	position: absolute;
	top: 100px;
	left: 0;
	z-index: -1;
	display: block;
	width: 40px;
	height: 40px;
	border-top-left-radius: 50%;
	border-bottom-left-radius: 50%;
	background: #444;
	color: #fff!important;
	text-align: center;
	font-size: 20px!important;
	line-height: 40px;
	opacity: 1;
	transition: all .1s linear .2s;
	transform: translateX(-60px);
	&:hover {
		&:before {
			animation-play-state: running;
		}
	}
}

#theme-settings .theme-settings-open-btn:before {
	content: "";
	width: 18px;
	height: 18px;
	display: block;
	background-size: 100% 100%;
	position: absolute;
	background-image: url("data:image/png;base64,");
	left: 50%;
	top: 50%;
	transform: translate(-50%, -50%);
	margin-left: 2px;
	animation: rotating 5s linear infinite;
	animation-play-state: paused;
	top: 11px;
	left: 12px;
}

[dir=rtl] #theme-settings .theme-settings-open-btn {
	border-radius: 0;
	border-top-right-radius: 50%;
	border-bottom-right-radius: 50%
}

[dir=rtl] #theme-settings .theme-settings-open-btn:before {
	margin-left: -2px
}

#theme-settings.theme-settings-open .theme-settings-open-btn {
	opacity: 0;
	transition-delay: 0s;
	transform: none!important
}

#theme-settings .theme-settings-close-btn {
	position: absolute;
	top: 50%;
	right: 10px;
	display: block;
	font-size: 20px;
	transform: translateY(-50%);
	font-size: 2em;
}

#theme-settings>h5 {
	flex: 0 0 auto
}

#theme-settings .theme-settings-inner {
	position: relative;
	overflow: auto;
	flex: 0 1 auto;
	opacity: 1;
	transition: opacity .2s;
	padding: 10px 20px;
}

#theme-settings .theme-settings-inner>div:first-child>hr:first-of-type {
	display: none!important
}

#theme-settings .theme-settings-inner>div:first-child>h5:first-of-type {
	padding-top: 0!important
}

#theme-settings .theme-settings-themes-inner {
	position: relative;
	opacity: 1;
	transition: opacity .2s;
	
}

#theme-settings .theme-settings-box {
	margin: 0;
	padding: 0 10px;
	padding-bottom: 10px;
}

#theme-settings .theme-settings-theme-item {
	display: -webkit-box;
	display: -ms-flexbox;
	display: flex;
	-webkit-box-align: center;
	align-items: center;
	-ms-flex-align: center;
	-webkit-box-flex: 1;
	-ms-flex: 1 1 100%;
	flex: 1 1 100%;
	-webkit-box-pack: justify;
	-ms-flex-pack: justify;
	justify-content: space-between;
	margin-bottom: 10px;
	padding: 0 24px;
	width: 100%;
	cursor: pointer
}

#theme-settings .theme-settings-theme-item input {
	position: absolute;
	z-index: -1;
	opacity: 0
}

#theme-settings .theme-settings-theme-item input~span {
	opacity: .25;
	transition: all .2s
}

#theme-settings .theme-settings-theme-item .theme-settings-theme-checkmark {
	display: inline-block;
	width: 6px;
	height: 12px;
	border-right: 1px solid;
	border-bottom: 1px solid;
	opacity: 0;
	transition: all .2s;
	transform: rotate(45deg)
}

[dir=rtl] #theme-settings .theme-settings-theme-item .theme-settings-theme-checkmark {
	border-right: none;
	border-left: 1px solid;
	-webkit-transform: rotate(-45deg);
	-ms-transform: rotate(-45deg);
	transform: rotate(-45deg)
}

#theme-settings .theme-settings-theme-item:hover input:not([disabled])~span,
#theme-settings .theme-settings-theme-item input:checked:not([disabled])~span,
#theme-settings .theme-settings-theme-item input:checked:not([disabled])~span .theme-settings-theme-checkmark {
	opacity: 1
}

#theme-settings .theme-settings-theme-colors span {
	display: block;
	margin: 0 1px;
	width: 10px;
	height: 10px;
	border-radius: 50%;
	-webkit-box-shadow: 0 0 0 1px rgba(0, 0, 0, .1) inset;
	box-shadow: inset 0 0 0 1px rgba(0, 0, 0, .1)
}

#theme-settings.theme-settings-loading-theme .theme-settings-themes-inner,
#theme-settings.theme-settings-loading .theme-settings-inner {
	opacity: .2
}

#theme-settings.theme-settings-loading-theme .theme-settings-themes-inner:after,
#theme-settings.theme-settings-loading .theme-settings-inner:after {
	content: "";
	position: absolute;
	top: 0;
	right: 0;
	bottom: 0;
	left: 0;
	z-index: 999;
	display: block
}

#theme-settings .theme-settings-bg-item.disabled,
#theme-settings .theme-settings-navbarBg-inner[disabled] .theme-settings-bg-item,
#theme-settings .theme-settings-sidenavBg-inner[disabled] .theme-settings-bg-item {
	opacity: .2;
	cursor: default
}

#theme-settings .theme-settings-bg-item {
	display: block;
	float: left;
	margin: 3px;
	width: 16px;
	height: 16px;
	border-radius: 2px;
	-webkit-box-shadow: 0 0 0 1px rgba(0, 0, 0, .1) inset;
	box-shadow: inset 0 0 0 1px rgba(0, 0, 0, .1);
	cursor: pointer
}

#theme-settings .theme-settings-bg-item.active {
	-webkit-box-shadow: 0 0 0 2px #000;
	box-shadow: 0 0 0 2px #000
}

#theme-settings .theme-settings-bg-item input {
	position: absolute;
	visibility: hidden;
	clip: rect(0, 0, 0, 0);
	pointer-events: none
}

#theme-settings {
	ul {
		margin: 0;
		padding-left: 16px;
		list-style: decimal;
		> li {
			margin-bottom: 5px;
		}
	}
}

.layout-sidenav-100vh #theme-settings {
	height: 100vh;
}

[dir=rtl] #theme-settings {
	right: auto;
	left: 0;
	transform: translateX(-250px)
}

[dir=rtl] #theme-settings .theme-settings-open-btn {
	right: 0;
	left: auto;
	-webkit-transform: translateX(60px);
	-ms-transform: translateX(60px);
	transform: translateX(60px)
}

[dir=rtl] #theme-settings .theme-settings-close-btn {
	right: auto;
	left: 0
}

[dir=rtl] #theme-settings .theme-settings-bg-item {
	float: right
}

.theme-setting-box {
	.form-check {
		input {
			height: 15px;
			width: 15px;
		}
	}
	label {
		font-size: 14px !important;
		margin-left: 5px;
		text-transform: none !important;
		padding-left: 0 !important;
	}
}

// grid overlay 
#grid-overlay {
	position: fixed;
	top: 0;
	left: 0;
	width: 100%;
	height: 100%;
	z-index: 99999;
	&.show {
		display: block !important;
	}
	.container {
		height: 100%;
		.row {
			height: 100%;
			.column {
				background: rgba(255,0,0,.4);
				height: 100%;
			}
		}
	}
}

.dev-headline {
	border: 1px solid red;
	color: red;
}

@-webkit-keyframes rotating {
	from {
		-webkit-transform: rotate(0deg);
		-o-transform: rotate(0deg);
		transform: rotate(0deg);
	}

	to {
		-webkit-transform: rotate(360deg);
		-o-transform: rotate(360deg);
		transform: rotate(360deg);
	}
}

@keyframes rotating {
	from {
		-ms-transform: rotate(0deg);
		-moz-transform: rotate(0deg);
		-webkit-transform: rotate(0deg);
		-o-transform: rotate(0deg);
		transform: rotate(0deg);
	}

	to {
		-ms-transform: rotate(360deg);
		-moz-transform: rotate(360deg);
		-webkit-transform: rotate(360deg);
		-o-transform: rotate(360deg);
		transform: rotate(360deg);
	}
}

.rotating {
	-webkit-animation: rotating 2s linear infinite;
	-moz-animation: rotating 2s linear infinite;
	-ms-animation: rotating 2s linear infinite;
	-o-animation: rotating 2s linear infinite;
	animation: rotating 2s linear infinite;
}