.modal-dialog {
    box-shadow: $box-shadow-lg;
}


.modal-content {
    border-radius: 10px;
    border: #fff;
}

.modal-header {
    background: transparent url("../images/bg-modal-header.png") no-repeat top left;
    background-size: cover;
    border-bottom: none;
    @include media-breakpoint-down(md) {
        padding: 1.5rem 2rem;
    }
}

.modal-title {
    font-size: $font-size-base * 3.75;
    color: $white;
    text-transform: uppercase;
    font-weight: $font-weight-bold;
    @include media-breakpoint-down(md) {
        font-size: $font-size-base * 2;
    }
}

.modal-body {
    font-size: $font-size-base * 1.5;
    padding-top: 2rem;
    color: $gray-600;
    margin-right: 20px;
    margin-top: 20px;
    

    @include media-breakpoint-down(md) {
        padding: 1.5rem;
        font-size: $font-size-base * 1
    }

    h4 {
        font-size: $font-size-base * 2;
        margin-bottom: 2rem;
        font-weight: $font-weight-bold;
        @include media-breakpoint-down(md) {
            font-size: $font-size-base * 1.5;
            margin-bottom: 1.5rem;
        }
    }
}

.modal-footer {
    border-top: none;
    justify-content: flex-start;
    position: relative;
    padding-top: 1rem;

    @include media-breakpoint-down(md) {
        padding: 1.5rem;
    }

    $temp-gradient-height: 110px;

    &::before {
        content: "";
        background: linear-gradient(to bottom, rgba(255, 255, 255, 0) 10%, rgba(255, 255, 255, 1) 80%);
        position: absolute;
        top: -($temp-gradient-height);
        left: 0;
        height: $temp-gradient-height;
        width: calc(100% - 30px);
        pointer-events: none;
        @include media-breakpoint-down(md) {
            display: none;
        }
    }

    form {
        margin-bottom: 3rem;
        @include media-breakpoint-down(md) {
            margin-bottom: 1rem;
        }
        
        label {
            color: $gray-600;
            text-transform: none;
            font-size: $font-size-base * 1.5;
            @include media-breakpoint-down(md) {
                font-size: $font-size-base * 1;
            }
        }
        .custom-control-label:after,
        .custom-control-label:before {
            top: 4px;
        }
    }

    .btn {
        + .btn {
            margin-left: 2rem;
            @include media-breakpoint-down(md) {
                margin-left: 1rem;
            }
        }
    }

}


.modal {
    ::-webkit-scrollbar {
        width: 10px;
    }
    ::-webkit-scrollbar-track {
        background: $white;
    }

    ::-webkit-scrollbar-thumb {
        background: #dddddd;
    }

    ::-webkit-scrollbar-thumb:hover {
        background: $orange;
    }
}