$temp-contact-first-breakpoint: 1300px;
.hero-overlay-contact {
    position: absolute;
    right: 0;
    bottom: 30%;
    z-index: 2;

    @include media-breakpoint-down(md) {
       display: none;
    }

    .site-header & {
        position: static;
        @include media-breakpoint-down(md) {
            display: block;
        }
    }

    // line
    &::before {
        content: "";
        position: absolute;
        top: -20px;
        left: -35px;
        right: auto;
        background-color: $primary;
        height: 4px;
        width: 48px;
        transition: $transition-custom;

        .site-header & {
            display: none;
        }


        @media (max-width: $temp-contact-first-breakpoint) {
            left: 80%;
            right: 0;


        }

    }

    /*
    @include media-breakpoint-down(md) {
        top: 40vh;
    }
    */

    ul {
        list-style: none;
        margin-bottom: 0;
        padding: 0;

        > li {
            // background-color: $orange;
            margin-bottom: .5rem;
            transition: $transition-base;
            position: relative;

            a {
                font-size: $font-size-base * 1.5;
                text-decoration: none;
                color: $white;
                padding: .5rem;
                display: block;

                &:first-child {
                    width: 46px;
                    height: 46px;

                    @include media-breakpoint-down(sm) {
                        width: 40px;
                        height: 40px;
                    }
                }

                > span {
                    color: $secondary;
                    font-weight: $font-weight-bold;
                    transition: $transition-base;
                    
                    .site-header & {
                        color: $white;
                    }

                }


                @include media-breakpoint-down(sm) {
                    padding: .5rem;
                    font-size: $font-size-base * 1.5;
                }
            }

            
            &:hover {
                a > span {
                    color: $primary;
                }
              
            }
            
        }
    }

    // list languages

    .list-languages {

        transition: $transition-custom;
        right: 0;

        @media (max-width: $temp-contact-first-breakpoint) {
            position: relative;
            right: -($sticky-sidebar-width);
            width: $sticky-sidebar-width;
            
            // box-shadow: 0px 5px 10px rgba(0, 0, 0, 0.1);
        }

        
        .site-header & {
            @media (max-width: $temp-contact-first-breakpoint) {
                right: -35px;
            }
        }

        &.is--open {
            right: 0;
            @media (max-width: $temp-contact-first-breakpoint) {
                > a {
                    background-color: $white;
                }
            }
           .site-header & {
            right: -35px;
            @media (max-width: $temp-contact-first-breakpoint) {
                >a {
                    background-color: transparent;
                }
            }
           }
        }

        .icon-phone,
        .icon-email,
        .icon-language {
            position: absolute;
            left: -40px;
            top: 0;
            // background-color: $orange;
            color: $primary;

            &::before {
                position: relative;
                top: 5px;
            }
            
            .site-header & {
                color: $white;
            }

            >img {
                height: 30px;
                width: 30px;
                vertical-align: bottom;
                border-radius: 50%;
                object-fit: cover;
                position: relative;
                top: -1px;


                @include media-breakpoint-down(sm) {
                    max-width: 24px;
                    margin-top: -3px;
                    height: 23px;
                    width: 25px;
                }
            }
            
            /*
            &:hover {
                background-color: darken($orange, 10%);
            }
            */
        }

        .icon-email {
            &::before {
                top: 3px;
                font-size: 1.3rem;
            }
        }

        a:not(.icon-language):not(.icon-phone):not(.icon-email) {
            color: $primary;
            font-size: $font-size-base * 1.25;

            @include media-breakpoint-down(sm) {
                font-size: $font-size-base * 1;
            }

            img {
                width: 30px;
                height: 30px;
                margin-right: .5rem;
                margin-top: -2px;
                border-radius: 50%;
                object-fit: cover;

                @include media-breakpoint-down(sm) {
                    width: 25px;
                    height: 25px;
                }
            }

            /*
            &:hover {
                background-color: $orange;
                color: #fff;
            }
            */
        }
    }

}