// -----------------------------------------------------------------------------
// This file contains all application-wide Sass mixins.
// ----------------------------------------------------------------------------

@mixin hero-overlay-form {
    .hero-overlay-form {
        margin-top: 3rem;
        display: flex;
        

        @include media-breakpoint-down(lg) {
            margin-top: 1.5rem;
        }

        @include media-breakpoint-down(sm) {
            display: block;
        }

        .hero-overlay-button {
            display: flex;
            @include media-breakpoint-down(sm) {
                display: block;
            }
            > a {
                + a {
                    margin-left: auto;

                    @include media-breakpoint-down(sm) {
                        font-size: $font-size-base * 1;
                    }
                }
            }
        }

        .form-control {
            border-radius: 6px;
            // max-width: 67%;
            font-style: italic;
            border: 4px solid $primary;
            border-radius: 0;
            font-size: 2rem;
            color: $primary;
            height: calc(1.55em + 1rem + 30px);
            box-shadow: none;

            &:focus {
                border-color: darken($primary, 7.5%);
            }

            @include media-breakpoint-down(md) {
                font-size: 1.3rem;
            }

            @include media-breakpoint-down(sm) {
                font-size: 0.875rem;
            }
            

            /*
            + .form-control {
                margin-top: 1rem;
            }
            */

            &::placeholder,
            &::-webkit-input-placeholder {
                color: $primary;
            }

        }

        .hero-overlay-button {
            position: relative;
            z-index: 1;
            margin-left: auto;
            min-width: 320px;

            @include media-breakpoint-down(sm) {
                margin-bottom: 2rem;
            }

            >button {
                font-size: 2rem;
                // flex: 1;
                text-align: left;
                padding-left: 40px;
                padding-right: 40px;
                display: flex;
                align-items: center;
                font-weight: $font-weight-bold;
                
                @include media-breakpoint-up(lg) {
                    flex: 1;
                }

                @include media-breakpoint-down(sm) {
                    font-size: 0.875rem;
                    padding-left: 20px;
                    padding-right: 20px;
                }

                @include media-breakpoint-down(sm) {
                    height: calc(1.55em + 1rem + 10px);
                }

                > span {
                    margin-left: 1rem;
                    @include media-breakpoint-up(lg) {
                        margin-left: auto;
                    }
                }
            }
        }

    }
}