// -----------------------------------------------------------------------------
// This file contains all styles related to the footer of the site/application.
// -----------------------------------------------------------------------------

.site-footer {

    .site-footer-inner {
        // background-color: rgba($primary, .2);

        .logobar {
            > .logobar-item {
                
                @include media-breakpoint-down(lg) {
                    margin-bottom: 2rem;
                }

                a {
                    margin-right: 2rem;
                    text-decoration: none;
                    &:hover {
                        text-decoration: none;
                    }
                }

                &.order-7 {
                    a {
                        margin-right: 0;
                    }
                }

            }
            
        }

        /*
        &.is--second {
            background-color: #e2e5e7;

            @include media-breakpoint-up(lg) {
                .logobar-item {
                    a:not(.no--transition) {
                        img,
                        svg {
                            transition: $transition-custom;
                            filter: grayscale(100%);
                        }
                    }

                    &:not(.no--transition):hover {
                        img,
                        svg {
                            filter: initial;
                        }
                    }
                }
            }
        }
        */



            

    }

    .site-bottombar {
        background-color: $primary;
        color: $white;

        p {
            font-size: 1rem;
        }

        a {
            color: $white;
            // transition: $transition-base;

            @include media-breakpoint-down(lg) {
                font-size: $font-size-base * 1.3;
            }

            /*
            &:hover {
                color: $primary;
            }
            */
        }

        .list-social-media {
            > li {

                &:not(:last-child) {
                    margin-right: 1rem;
                }

                a {
                    text-decoration: none;
                    font-size: $font-size-base * 2;

                    color: $white;

                    // transition: $transition-custom;
                    // filter: grayscale(100%);

                    @include media-breakpoint-down(lg) {
                       font-size: $font-size-base * 3;
                    }
                    
                    /*
                    &.icon-facebook {
                        color: #1777f2;
                    }
                    &.icon-twitter {
                        color: #1da1f2;
                    }
                    &.icon-youtube {
                        color: #f24423;
                    }
                    */
                    &.icon-insta {
                        // color: #f77737;
                        font-size: 1.55rem;
                        position: relative;
                        top: -3px;
                        @include media-breakpoint-down(lg) {
                            font-size: $font-size-base * 2.55;
                        }
                    }

                    /*
                    &:hover {
                        filter: initial;
                    }
                    */

                }
            }
        }

    }

    

}