.scroll-to-top {
    font-size: 20px;
    text-align: center;
    color: #fff;
    text-decoration: none;
    position: fixed;
    bottom: 5rem;
    right: 3rem;
    // opacity: 0;
    opacity: 1;
	display: block;
	visibility: hidden;
	visibility: visible;
    border-radius: 50%;
    background-color: $primary;
    border: 1px solid $primary;
    width: 4rem;
    height: 4rem;
    line-height: 4rem;
    z-index: 9999;
    outline: 0;
    // transform: translateY(5rem);
    transition: $transition-base;
    // mix-blend-mode: screen;
    color: #fff;
    font-weight: $font-weight-bold;
    &::after {
        display: inline-block;
        content: "";
        padding: 5px;
        border-top: 3px solid;
        border-right: 0;
        border-bottom: 0;
        border-left: 3px solid;
        transform: rotate(45deg);
        position: relative;
        top: 3px;
    }
    
    @include media-breakpoint-up(xl) {
        &.is--visible {
            opacity: 1;
            transform: translateY(0);
            visibility: visible;
        }

        &:hover {
            color: #fff;
            background: rgba($primary, .8);
        }

    }


}