.nav-pills {

    @include media-breakpoint-up(lg) {
        margin-top: -3rem;
        margin-bottom: 2.5rem !important;
    }

    .nav-item {
        .nav-link {
            text-transform: uppercase;
            font-size: $font-size-base * 1.25;
            font-weight: $font-weight-bold;
            color: $gray-600;
            padding-left: 0;
            padding-right: 0;

            @include media-breakpoint-down(xs) {
                font-size: $font-size-base * 1;
            }

            &.active {
                color: $orange;
            }

            &::after {
                content: "";
                width: 2px;
                height: 30px;
                background-color: $gray-600;
                display: inline-block;
                position: relative;
                top: 7px;
                margin: 0 1rem;
                @include media-breakpoint-down(xs) {
                    margin: 0 .8rem;
                }
            }

            // make different colors
            &#pills-adults-tab {
                color: $adults;
            }

            &#pills-minors-tab {
                color: $minors;
            }

            &#pills-children-tab {
                color: $children;
            }

            &#pills-guardians-tab {
                color: $guardians;
            }


            &#pills-instruction-tab {
                color: $instruction;
            }

        }



        &:last-child {
            .nav-link {
                &::after {
                    background-color: transparent;
                    margin: 0;
                }
            }
        }
    }



}